.Left-column{
    margin-left: 1em;

}
.EmptySpace{
    height: 50px;
    width: 100%;
}

.Skills{
    opacity: 0;
}

.right-column{
  width: 500px;
  height: 500px;
}

.typewriter .Skills {
  opacity: 1;
  overflow: hidden;
  max-width: 13em;
  text-align: start;
  border-right: 0.15em solid black; /* The typwriter cursor */
  white-space: nowrap; /* Keeps the content on a single line */
  margin: 0 ; /* Gives that scrolling effect as the typing happens */
  letter-spacing: 0.1em; /* Adjust as needed */
  animation: typing 1.5s steps(30, end), blink-caret 0.75s step-end infinite;
}

/* The typing effect */
@keyframes typing {
    from {
      width: 0;
    }
    to {
      width: 13em;
    }
  }
  
  /* The typewriter cursor effect */
  @keyframes blink-caret {
    from,
    to {
      border-color: transparent;
    }
    50% {
      border-color: black;
    }
  }
  
  .Skill-Words{
    max-width:400px;
    
}

@media screen and (max-width: 1360px){
  .content-row{
    display: flex;
    flex-direction: column;
  }
  .Content-About-P{
    width: 100vw;
    height: auto;
  }
}

@media screen and (max-width: 500px){
  .typewriter .Skills {
    max-width:40vh;
  }
    .Skills{
        font-size: 3vh;
    }
    .Skill-Words{
        max-width: 200px;
        font-size: 2vh;
    }
    .right-column{
      max-width: 80vw;
      max-height:80vh
    }
}