}

/* The typing effect */
@-webkit-keyframes typing {
    from {
      width: 0;
    }
    to {
      width: 13em;
    }
  }
@keyframes typing {
    from {
      width: 0;
    }
    to {
      width: 13em;
    }
  }
  
  /* The typewriter cursor effect */
  @-webkit-keyframes blink-caret {
    from,
    to {
      border-color: transparent;
    }
    50% {
      border-color: black;
    }
  }
  @keyframes blink-caret {
